let now = null;
export const current = () => now;

export const empty = [];
export const setup = [];

export const $ = (value, args) =>
  (typeof value) === (typeof $) ? value.apply(null, args)
  : value;

export const diff = (a, b) =>
  (a.length !== b.length || a.some(diverse, b));

export const stacked = function(id) {
  return runner => {
    const state = {i: 0, stack: []};
    runner[id] = state;
    runner.before.push(() => {
      state.i = 0;
    });
  };
}

let id = 0;
export const uid = () => '_$' + id++;

export const unstacked = id => {
  const {[id]: state, update} = now;
  const {i, stack} = state;
  state.i++;
  return {i, stack, update, unknown: i === stack.length};
};

export default augmentee => {
  const currentRunner = runner($omeFunction);
  each(setup, currentRunner);
  $omeFunction.reset = () => {
    each(currentRunner.reset, currentRunner);
    for (const key in currentRunner) {
      if (/^_\$/.test(key))
        currentRunner[key].stack.splice(0);
    }
  };
  return $omeFunction;
  function $omeFunction() {
    const prev = now;
    now = currentRunner;
    const {_, before, after, external} = currentRunner;
    try {
      let result;
      do {
        _.$omething = _._flip = false;
        each(before, currentRunner);
        result = augmentee.apply(_.context = this, _.argus = arguments);
        each(after, currentRunner);
        if (external.length)
          each(external.splice(0), result);
      } while (_._);
      return result;
    }
    finally {
      _.$omething = true;
      now = prev;
    }
  }
};

const each = (arr, value) => {
  const {length} = arr;
  let index = 0;
  while (index < length)
    arr[index++](value);
};

const runner = fun => {
  const _ = {
    _flip: true,
    $omething: true,
    context: null,
    argus: null
  };
  return {
    _: _,
    before: [],
    after: [],
    external: [],
    reset: [],
    update: () => _.$omething ? fun.apply(_.context, _.argus) : (_._flip = true)
  };
};

function diverse(value, i) {
  return value !== this[i];
}
